.book {
  --color: #4f2ee8;
  --duration: 6.8s;
  width: 32px;
  height: 12px;
  zoom: 1.5;
  margin: 32px 0 0;
  position: relative;
}

.book .inner {
  width: 32px;
  height: 12px;
  transform-origin: 2px 2px;
  animation: book var(--duration) ease infinite;
  position: relative;
  transform: rotateZ(-90deg);
}

.book .inner .left, .book .inner .right {
  width: 60px;
  height: 4px;
  background: var(--color);
  border-radius: 2px;
  position: absolute;
  top: 0;
}

.book .inner .left:before, .book .inner .right:before {
  content: "";
  width: 48px;
  height: 4px;
  background: inherit;
  border-radius: 2px;
  position: absolute;
  top: -10px;
  left: 6px;
}

.book .inner .left {
  transform-origin: 58px 2px;
  animation: left var(--duration) ease infinite;
  right: 28px;
  transform: rotateZ(90deg);
}

.book .inner .right {
  transform-origin: 2px 2px;
  animation: right var(--duration) ease infinite;
  left: 28px;
  transform: rotateZ(-90deg);
}

.book .inner .middle {
  width: 32px;
  height: 12px;
  border: 4px solid var(--color);
  border-top: 0;
  border-radius: 0 0 9px 9px;
  transform: translateY(2px);
}

body.dark-theme .book {
  --color: #eac645;
}

